// Variable
:root {
  --color-primary-text: rgb(105, 105, 105);
  --color-headline: #b5e0ea;
  --color-banner-social: #808080;
}

// Global
body {
  margin: 0;
  font-family: "Lato", sans-serif;
}

// Change properties of elements when a user select the element
p::selection,
h1::selection,
h2::selection,
h3::selection,
a::selection {
  background-color: var(--color-headline);
}

p {
  color: var(--color-primary-text);
  line-height: 2rem;
}

.title {
  color: black;
  font-size: 33px;
}

@media (min-width: 1024px) {
  .title {
    font-size: 40px;
  }
}

h1 {
  color: black;
  span {
    color: var(--color-headline);
  }
}

.u-margin-intro {
  margin: 50px 0px;
}

// Header
header {
  padding-top: 20px;
}
figure {
  margin-top: 60px;
  img {
    border-radius: 5px;
  }
}
.section-header {
  padding-bottom: 0px;
}
// Component -> Navbar
.navbar-end a {
  padding: 10px 32px;
  transition: all 0.25s ease;
}
.container-menu {
  text-align: center;
  a {
    transition: all 0.25s ease;
  }
  a:hover {
    p {
      color: #2caeba;
    }
    border-bottom: 1px solid #2caeba;
  }
}
// Component -> Hero
.banner-social {
  display: flex;
  align-items: center;
  justify-content: space-between;

  .line {
    padding: 0;
  }

  span {
    display: block;
    width: 80%;
    border-top: 2px solid var(--color-banner-social);
  }
  a {
    display: inline-block;
    color: var(--color-banner-social);
    padding: 10px 30px 10px 0px;
  }
  .icon-git,
  .icon-lkd {
    transition: all 0.25s ease;
    font-size: 26px;
  }
  .icon-git:hover {
    color: black;
  }
  .icon-lkd:hover {
    color: rgb(0, 119, 181);
  }
}
.bg-hero {
  background-image: url("./img/bg-hero.png");
  background-repeat: no-repeat;
  background-position: 0px 10px;
  background-size: 180px;
  padding-top: 50px;
  h1 {
    font-weight: 600;
    font-size: 26px;
  }
  @media (min-width: 1024px) {
    h1 {
      font-size: 50px;
    }
  }
  .btn-contact {
    border: none;
    margin-top: 14px;
    background-color: #2caeba;
    text-transform: uppercase;
    color: white;
    font-size: 18px;
    font-weight: 600;
    transition: all 0.5s ease;
  }
  .btn-contact:hover {
    background-color: var(--color-headline);
    color: #2caeba;
  }
}

// Component -> Photo

// Section -> About
.section-about {
  padding-top: 0;
}

// Section -> Skills

.container-skills {
  p {
    color: black;
    font-weight: 700;
    padding: 6px;
    span {
      font-size: 20px;
      padding-right: 10px;
      color: var(--color-headline);
    }
  }
  .column {
    padding: 0;
  }
}

// Section -> Projects

// Component -> Project
.card {
  margin-bottom: 70px;
  // box-shadow: none;
  .card-image {
    padding-bottom: 24px;
  }
  .card-content {
    // padding: 24px 0px;
    h3 {
      font-size: 30px;
      padding-bottom: 20px;
    }
    p {
      font-size: 16px;
      line-height: 1.8rem;
    }
  }
  img {
    border-radius: 5px;
    transition: all 0.4s ease;
  }
  .card-footer-item {
    color: black;
    font-weight: 600;
    text-transform: uppercase;
    transition: all 4s ease;
  }
}
.card:hover {
  img {
    transform: scale(1.02);
  }
  .card-footer-item {
    text-decoration: underline;
    text-decoration-thickness: 2.6px;
  }
}

// Section -> Contact
.section-contact {
  a {
    color: black;
    font-weight: 700;
    font-size: 16px;
    border-bottom: 2px solid var(--color-headline);
    transition: all 0.5s ease;
  }
  a:hover {
    // transition: all 2s ease;
    // border-bottom: none;
    border-width: thick;
  }
}

// Section -> Footer
.container-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 0px;
}

// Project.jsx

.container-project {
  h1 {
    font-size: 60px;
    margin: 26px 0px;
    color: black;
  }
  h2 {
    font-size: 30px;
    margin: 20px 0;
    color: black;
  }
  figure {
    margin: 60px 0px;
  }
}

.u-margin-txt {
  margin: 50px 0px;
}

table {
  width: 100%;
  td {
    padding: 4px 0;
    color: black;
    font-weight: 300;
    a {
      font-weight: 900;
    }
  }
  th {
    color: black;
    padding: 1px 1px 20px;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 900;
  }
}

@media (min-width: 1024px) {
  h1 {
    font-size: 40px;
  }
  .u-padding-right {
    padding-right: 30%;
  }
  .form-contact {
    margin: 150px auto;
  }
}

.golang-contact img {
  height: 80%;
  width: 80%;
  margin: 0 auto;
}

.golang-contact {
  margin: 0;
}

.form-contact {
  margin: 90px 0px;
  button {
    transition: all 0.5s ease;
    // font-weight: 700;
    color: white;
    padding-right: 30px;
    padding-left: 30px;
    // border-color: var(--color-headline);
    background-color: #47535e;
  }
  button:hover,
  button:focus {
    color: white;
  }
}
